.navbarPadding{
    padding: 16px 0;
    margin-bottom: 32px;
}
.darkModeToggle{
    width: 50px;
    height: 25px;
    position: absolute;
    right: 8%;
    display: block;
    background: var(--component-background-color);
    border-radius: 200px;
    box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4),
        inset 0px -5px 15px rgba(255, 255, 255, 0.4);
    cursor: pointer;
    transition: 0.3s;
}
.darkModeToggle::after{
    content: "";
    width: 24px;
    height: 24px;
    position: absolute;
    top: 1px;
    left: 2px;
    background: var(--toogle-color);
    /* linear-gradient(180deg, #ffcc89, #d8860b); */
    border-radius: 180px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}
.darkModeToggle i{
    position: absolute;
    width: 40px;
    top: 5px;
    z-index: 100;
}
.darkModeToggle .sun{
    left: -7px;
    color: var(--sun-color);
    transition: 0.3s;
}
.darkModeToggle .moon{
    left: 19px;
    color: var(--moon-color);
    transition: 0.3s;
}
.darkModeToggleInput{
    width: 0;
    height: 0;
    visibility: hidden;
}

.darkModeToggleInput:checked+label:after {
    left: 49px;
    transform: translateX(-100%);
    background: var(--toogle-color);
    /* linear-gradient(180deg, #777, #3a3a3a); */
}
.title{
    color: var(--color-primary);
}
.menuLink{
    text-decoration: none;
    color: var(--text-color);
    text-align: center;
    font-family: Julius Sans One;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 2.2px;
}
.offcanvas{
    background: var(--background-color);
}
.buttonClose{
    color: var(--text-color);
    font-size: 22px;
}
.offcanvasBody{
    justify-content: space-around;
    display: flex;
    flex-direction: column;
    /* height: 80%; */
    padding: 0 0 50% 0;
}